import '../node_modules/purecss/build/grids-min.css'
import '../node_modules/purecss/build/grids-responsive-min.css'

import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



