            <footer class="foot pure-u-1">
                <section class="pure-g">
                    <section class="pure-u-md-1-8 pure-u-lg-1-4"></section>
                    <section class="pure-u-1 pure-u-md-3-4 pure-u-lg-1-2 infoP">
                        <section class="pure-u-1 design">
                            <span>@2017 Design by Zhouyang Wu</span>
