                        </section>
                        
                        <section class="pure-u-1 mail">
                            <address class="name">
                                <span class="iconfont icon-r-gmail"></span>
                                <a href="mailto:oceaniawzy@gmail.com">oceaniawzy@gmail.com</a> 
                            </address>
                        </section>
                    </section>
                </section>
                <section class="pure-g thanksP">
                    <section class="pure-u-1 thanks">
                        特别鸣谢
                    </section>
                    <section class="pure-u-1 pure-u-md-1-5 link">
                        <a target="_blank" href="https://github.com/vuejs/vue">Vuejs</a>
                    </section>
                    <section class="pure-u-1 pure-u-md-1-5 link">
                        <a target="_blank" href="https://github.com/yahoo/pure">Purecss</a>
                    </section>
                    <section class="pure-u-1 pure-u-md-1-5 link">
                        <a target="_blank" href="https://github.com/wuzhouyang/OCask.js">OCask.js</a>
                    </section>
                    <section class="pure-u-1 pure-u-md-1-5 link">
                        <a target="_blank" href="https://github.com/sachinchoolur/lightGallery">lightgallery.js</a>
                    </section>
                    <section class="pure-u-1 pure-u-md-1-5 link">
                        <a target="_blank" href="https://github.com/noraesae/perfect-scrollbar">perfect-scrollbar</a>
                    </section>
                </section>
            </footer>
        </section>
    </section>
</template>

<script>
    import Ocask from 'Ocask.js'
    import 'lightgallery.js'
    import Ps from 'perfect-scrollbar'
    import '../../node_modules/lightgallery.js/dist/css/lightgallery.min.css'
    import '../../node_modules/perfect-scrollbar/dist/css/perfect-scrollbar.min.css'

    export default{
        name:'resume',
        mounted:function(){
            var _this=this
            window.addEventListener('scroll',this.bodyScroll)
            window.addEventListener('resize',function(){
                _this.$refs.projectsGallery.removeAttribute('style')
            })

            document.onkeydown = this.enterGet
            this.$refs.projectsGallery.style.width = this.$refs.projectsGallery.offsetWidth - this.getScrollBarWidth() + 'px'
            var oc=new Ocask({
                container:this.$refs.projectsGallery,
                baseHeight:200,
                imgArr:[
                    require('../assets/imgs/project/pcerp-1.jpg'),
                    require('../assets/imgs/project/pcerp-2.jpg'),
                    require('../assets/imgs/project/merp-1.gif'),
                    require('../assets/imgs/project/merp-2.gif'),
                    require('../assets/imgs/project/ruled.jpg'),
                    require('../assets/imgs/project/merp-4.jpg'),
                    require('../assets/imgs/project/merp-3.gif'),
                    require('../assets/imgs/project/datachart-1.gif'),
                    require('../assets/imgs/project/datachart-3.jpg'),
                    require('../assets/imgs/project/datachart-4.jpg'),
                    require('../assets/imgs/project/contact.gif'),
                    require('../assets/imgs/project/datachart-5.gif'),
                    require('../assets/imgs/project/wechat-bind.gif'),
                    require('../assets/imgs/project/formd.jpg'),
                    require('../assets/imgs/project/formd-gen.gif'),
                    require('../assets/imgs/project/formd-main-sm-1.gif'),
                    require('../assets/imgs/project/formd-main-sm-2.jpg'),
                    require('../assets/imgs/project/formd-main.jpg'),
                ],
                isGap:false,
                imgClass:'imgEff',
                responsive: true,
                lightGallery:true
            })
            oc.getImgs(function(){
                _this.loading=false
                _this.setBody(false)
            })
            this.$refs.myinfo.forEach((ref,index)=>{
                Ps.initialize(ref,{
                    suppressScrollY:true
                })
            })
            this.$refs.projectMain.forEach((ref,index)=>{
                Ps.initialize(ref)
            })
            this.bodyTop=this.$refs.body.offsetTop
        },
        methods:{
            setBody:function(flag){
                if(flag){
                    this.bodyShow = true
                    this.$refs.body.removeAttribute('style')
                    this.$refs.body.style.minHeight = this.bodyHeight + 'px'
                }else{
                    this.bodyHeight = this.$refs.body.offsetHeight
                    this.$refs.body.style.height = '0px'
                }
            },
            bodyScroll:function(){
                var scrollTop=document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
                if(this.bodyTop < scrollTop){
                    this.headShow=false
                }else{
                    this.headShow=true
                }
            },
            getScrollBarWidth:function(){
                var scrollBarWidth
                var helper = document.createElement('div')
                helper.style.cssText = "overflow:scroll;width:100px;height:100px;";
                document.body.appendChild(helper)
                scrollBarWidth = helper.offsetWidth - helper.clientWidth
                document.body.removeChild(helper)
                return scrollBarWidth
            },
            enterGet:function(e){
                var theEvent = e || window.event
                var code = theEvent.keyCode || theEvent.which || theEvent.charCode
                //enter
                if(code === 13){
                    this.setBody(true)
                }
            }
        },
        data:function(){
            return{
                bodyShow:false,
                headShow:true,
                loading:true,
                bodyHeight:0,
                bodyTop:0,
                aboutMes:[
                    {
                        name:'个人',
                        value:' 吴洲洋 | 1994-06-02 | 男 | 双子 | 深圳'
                    },
                    {
                        name:'教育',
                        value:'深圳信息职业技术学院 | 软件技术 | 大学英语四级'
                    },
                    {
                        name:'经历',
                        value:'川亿电脑深圳有限公司（2015-12至今）'
                    },
                    {
                        name:'职位',
                        value:'前端开发 | web开发'
                    }
                ],
                skills:[
                    {
                        name:'了解',
                        value:'Golang | Redis | Mocha'
                    },
                    {
                        name:'熟悉',
                        value:'Webpack | Gulp | Vuejs | Es6 | Ionic'
                    },
                    {
                        name:'熟练',
                        value:'Javascript | Typescript | Angular | Sass | Css'
                    },
                    {
                        name:'系统',
                        value:'Windows | Centos7 | OS X Sierra'
                    }
                ],
                projects:[
                    {
                        name:'公司首页',
                        tag:'公司',
                        tecs:[
                            'angularjs',
                            'sass',
                            'bootstrap',
                            'angular-strap',
                            'animate.css'
                        ],
                        main:`公司门户网站登陆首页，采用 angular1 + scss + bootstrap, 替换旧首页；由于个人感觉体验不好，
                              后续版本使用 angular1 + scss + angularStrap + animate.css 修改美化，布局更加舒服，更加流畅
                              的css动画，用户体验度提升。`
                    },
                    {
                        name:'个人简历',
                        tag:'个人',
                        tecs:[
                            'vuejs',
                            'sass',
                            'purecss'
                        ],
                        main:`个人简历，使用 vue-cli 搭建，使用purecss做响应式，scss做样式，纯手工设计。简单地使用vue单文件组件，以及选取合适的
                             字体，毕竟简历设计能代表自己的审美观，所以主要花费在调样式上，目前对其满意。`
                    },
                    {
                        name:'移动端微信绑定、通讯录',
                        tag:'公司',
                        tecs:[
                            'sngularjs',
                            'ionic-1',
                            'sass',
                            'gulp'
                        ],
                        main:`使用gulp + angularjs +　ionic1 + scss搭建的移动端项目，结合后台 api 实现内部用户通过微信绑定信息，
                              以及添加通讯录模块，用户可以查看通讯录人员具体信息。通讯录模块结合 angularjs 指令与模板写了一个递归
                              菜单树，以及结合 ionic1 组件实现下拉加载。`
                    },
                    {
                        name:'PC端ERP功能工具',
                        tag:'公司',
                        tecs:[
                            'angular',
                            'sass',
                            'bootstrap',
                            'webpack',
                            'ionic2',
                            'typescript',
                            'golang',
                            'redis'
                        ],
                        main:`PC端ERP功能集工具，个人工作内容的包括搭建项目、登陆页、菜单页、规则设计器、页面设计器，项目结构由本人
                             搭建，采用 webpack + angular 搭建，参考文档实现按需加载、treeshaking、aot等等，结合 gzip 初始首屏理论
                             只需100多kb；菜单页、规则设计器均使用响应式；页面设计器使用动态组件加载，结合拖拽插件动态生成可供生成ionic2
                             组件的 json 数据，后台结合 golang 及 redis 解析生成并实现二维码预览。`
                    },
                    {
                        name:'木桶布局小插件',
                        tag:'个人',
                        tecs:[
                            'ecmascript3',
                            'lightgallery.js'
                        ],
                        main:`在做个人简历的时候由于想要实现的效果目前的css还不能实现或者说兼容性很不好，所以手动写了一个图片布局的小插件，
                             纯使用原生es3 语法，这个插件的效果为使图片能够按照原比例、而且每一行都固定高度来布局；插件提供了多个配置项，
                            例如可以设置默认高度、响应式等等；插件集成 lightgallery.js,可以在点击图片的时候得到更好的效果。`
                    },
                    {
                        name:'PC端报表展示',
                        tag:'公司',
                        tecs:[
                            'angular',
                            'sass',
                            'bootstrap',
                            'webpack',
                            'typescript',
                            'highcharts',
                            'primeng'
                        ],
                        main:`公司数据报表展示器，结合 angular + highcharts + primeng 和后台数据 api 做数据的过滤处理然后通过
                             图表展示于前端，已经完善，表现的图表类型有行列式表格、柱状图、趋势图等；关于优化的，使用了内存缓存功能，
                             已经获取的数据会展示存于内存中，减少网络请求。`
                    },
                    {
                        name:'移动端内部审核',
                        tag:'公司',
                        tecs:[
                            'angularjs',
                            'ionic-1',
                            'oc-lazyload',
                            'sass',
                            'gulp'
                        ],
                        main:`使用gulp + angularjs +　ionic1 + scss搭建的移动端项目，结合后台 api 实现内部用户登录、主菜单、内部表单的审核
                             （通过、否决、转发等等）、批量快速审核等功能，提升内部工作人员的工作效率；项目使用 gulp 写了一套适合
                             本项目的 gulpfile，结合 oc-lazyload 使用按需加载、压缩分块，使得发布项目更加优化。`
                    },
                    {
                        name:'个人博客',
                        tag:'个人',
                        tecs:[
                            'hexo',
                            'gulp',
                            'git'
                        ],
                        main:`个人博客，采用静态技术hexo搭建，配合github、coding平台page服务做双线，采用markdown格式专注写博文，使用
                             不蒜子数据统计，来必力社会评论平台做评论功能；同时使用 gulp 做简单的部署前优化，关于hexo主题的，目前使用
                             第三方主题，在其基础上稍作修改，涉及的技术有 ejs、stylus 等。`
                    },
                    {
                        name:'Angular starter',
                        tag:'个人',
                        tecs:[
                            'angular',
                            'sass',
                            'webpack',
                            'typescript'
                        ],
                        main:`一个angular2(angular4)的starter，starter是拿来即用的一个项目结构，因为在做 PC 端项目的时候曾花费了不少时间
                             做构建、优化等等，所以在完成之际即把其单独抽取出来做一个可供以后多次使用的 starter，目前的 PC 项目都是由其直接
                             构建；starter支持 aot、treeshaking、lazyload、scss等等。`
                    }
                ]
            }
        }
    }
</script> 

<style lang="scss">
    @import '../assets/scss/_value.scss';
    //header 字体线条动画
    @mixin LRAnimate($lrf,$fpx,$lrs,$spx){
        $animation-name: unique-id();
        animation-name: $animation-name;
        @keyframes #{$animation-name}{
            from {#{$lrf}:#{$fpx}}
            to{#{$lrs}:#{$spx}}
        }
    }

    @mixin BeforeAndAfter(){
        content:'';
        display:block;
        position:absolute;
    }
    .resume-base{
        span{
            font-family:'Source-Han-Ligha050dfce518b79','Microsoft YaHei';
        }
        .r-head{
            background:url('../assets/imgs/header-sr.jpg') 0 0 no-repeat;
            background-size:cover;
            text-align:center;
            width:100%;
            height:100%;
            position:fixed;
            overflow:hidden;
            z-index:1;
            .r-head-col{
                position:absolute;
                height:100%;
                width:50%;
                top:50%;
                left:0;
                bottom:0;
                right:0;
                margin:auto;
                color:#fff;
                color:rgba(255,255,255,.7);
                @media screen and (max-width: 26em){
                    top:30%;
                }
                .r-head-name{
                    .r-hello,
                    .r-myname{
                        height:50px;
                        line-height:50px;
                        .hello,
                        .myname{
                            white-space:nowrap;
                            position:relative;
                            &:before,
                            &:after{
                                @include BeforeAndAfter();
                                background:$primaryColor;
                            }
                            &:before{
                                width:60%;
                                height:1px;
                            }
                            &:after{
                                height:130%;
                                width:1px;
                            }
                        }
                    }
                    .r-hello{
                        line-height:70px;
                        .hello{
                            font-size:2rem;
                            &:before{
                                top:-15px;
                                left:-50px;
                                animation:2s ease-out;
                                @include LRAnimate(left,100%,left,-50px);
                            }
                            &:after{
                                top:-30px;
                                left:-20px;
                                animation:  2s ease-out;
                                @include LRAnimate(left,100%,left,-20px);
                            }
                        }
                    }
                    .r-myname{
                        .myname{
                            font-family:'JetLinkBoldHanda0508807a18b79','Microsoft YaHei';
                            color:$primaryColor;
                            font-size:3.5rem;
                            &:before{
                                right:-50px;
                                bottom:-15px;
                                animation:  1.5s ease;
                                @include LRAnimate(right,100%,right,-50px);
                            }
                            &:after{
                                right:-20px;
                                bottom:-30px;
                                animation:  1.5s ease;
                                @include LRAnimate(right,100%,right,-20px);
                            }
                        }
                    }
                    @media screen and (min-width: 48em){
                        .r-hello{
                            text-align:right;
                            .hello{
                                padding-right:5px;
                            }
                        }
                        .r-myname{
                            text-align:left;
                            .myname{
                                padding-left:5px;
                            }
                        }
                    }
                    @media screen and (max-width: 48em){
                        .r-hello{
                            margin-bottom:1rem;
                        }
                    }
                }
                .r-head-statu{
                    margin-top:50px;
                    .statu{
                        position:relative;
                        &:after{
                        @include BeforeAndAfter();
                            width:60%;
                            height:2px;
                            left:20%;
                            bottom:-10px;
                            background:$primaryColor;
                        }
                    }
                }
                .r-head-contact{
                    margin-top:2px;
                    .r-contacts-ul{
                        padding:0px;
                        cursor:pointer;
                        .r-contacts-li{
                            list-style:none;
                            display:inline;
                            padding:3px;
                            a{
                                font-size:1.3rem;
                                text-decoration: none;
                                color:rgba(255,255,255,.3);
                                transition:all 0.3s;
                                &:hover{
                                    color:rgba(255,255,255,.8);
                                }
                            }
                        }
                    }
                }
                .r-head-enter{
                    margin-top:20px;
                    .enterBtn{
                        width:60px;
                        height:60px;
                        border-radius:50%;
                        background:rgba(255,255,255,.1);
                        border:none;
                        color:rgba(255,255,255,.5);
                        outline:none;
                        padding:10px;
                        cursor:pointer;
                        transition:all 1s;
                        position:relative;
                        font-family:'Source-Han-Ligha050dfce518b79';
                        &:before{
                            content:"";
                            display:block;
                            position:absolute;
                            height:120%;
                            width:120%;
                            border-radius:50%;
                            background:rgba(255,255,255,.1);
                            top:-10%;
                            left:-10%;
                        }
                        &:hover{
                            background:rgba(0,0,0,0.5);
                        }
                    }
                }
                .r-head-loading{
                    margin-top:20px;
                    @keyframes load{
                        from {transform:rotate(0deg);}
                        to {transform:rotate(360deg);}
                    }
                    .loading{
                        font-size:1.7rem;
                        display:inline-block;
                        animation:load 1s infinite linear;
                    }
                }
            }
        }
        .hide{
            transform:scale(0);
        }
        .r-body{
            box-shadow:-20px 0 50px rgba(0,0,0,0.5);
            position:absolute;
            z-index:2;
            top:75%;
            overflow:hidden;
            transition:all 1s;
            .main{
                background:#e6e6e6;
                padding-top:50px;
                padding-bottom:30px;
                .data-one{
                    text-align:center;
                    $borderColor:#d8d8d8;
                    .about-me,
                    .skill{
                        position:relative;
                        min-height:300px;
                        &:before{
                            @include BeforeAndAfter();
                            height:100%;
                            width:100%;
                            transform:skew(-12deg);
                        }
                        @media screen and (max-width: 48em){
                            &:before{
                                height:0;
                                width:0;
                                border:none;
                            }
                        }
                        .title{
                            font-size:1.5rem;
                            span{
                                display:inline-block;
                            }
                            .symbol{
                                color:#b9b9b9;
                                font-size:1.2rem;
                            }
                            .ch{
                                padding-right:10px;
                                border-right:2px solid $primaryColor;
                            }
                            .en{
                                padding-left:5px;
                                font-family:'Howard-Lighta0f513d4018b79';
                            }
                        }
                        .contents{
                            $bgcolor:#dcdcdc;
                            $color:#757575;
                            .one{
                                padding:20px 30px 0 30px;
                                @media screen and (max-width: 48em){
                                    padding:20px 10px 0 10px;
                                    .name,
                                    .value{
                                        font-size:0.8rem;
                                    }
                                }
                                .contain{
                                    .name,
                                    .value{
                                        width:100%;
                                        white-space: nowrap;
                                        text-align:center;
                                        height:35px;
                                        line-height:40px;
                                        background:$bgcolor;
                                        display:inline-block;
                                        color:$color;
                                        font-family:'Source-Han-Ligha050dfce518b79';
                                        position:relative;
                                        overflow:hidden;
                                    }
                                }
                            }
                        }
                    }
                    .about-me{
                        .contents{
                            text-align:left;
                        }
                        &:before{
                            border-right:1px solid $borderColor;
                            right:2px;
                        }
                    }
                    .skill{
                        &:before{
                            border-left:1px solid $borderColor;
                            left:2px;
                        }
                    }
                }
                .data-two,
                .data-five{
                    $dt-background:#363636;
                    margin-top:40px;
                    box-shadow:0 0 15px rgba(0,0,0,0.4);
                    background:$dt-background;
                    height:250px;
                    position:relative;
                    overflow:hidden;
                    &:before,
                    &:after,
                    .t-ba{
                        content:'';
                        position:absolute;
                        border-radius:50%;
                    }
                    &:before{
                        width:120px;
                        height:120px;
                        z-index:2;
                        background:lighten($dt-background,10);
                    }
                    &:after{
                        width:260px;
                        height:260px;
                        z-index:1;
                        background:lighten($dt-background,5);
                    }
                    .t-ba{
                        width:800px;
                        height:800px;
                        z-index:0;
                        background:lighten($dt-background,2);
                    }
                    .pic{
                        height:100%;
                        z-index:3;
                    }
                    @media screen and (max-width: 48em){
                        .pic{
                            display:none;
                        }
                    }
                    .thinkP{
                        z-index:3;
                        display:inline-table;
                        height:250px;
                        position:relative;
                        .think{
                            display:table-cell;
                            vertical-align:middle;
                            $color:#cacaca;
                            padding:0 20px 0 20px;
                            .fline,
                            .sline,
                            .tline{
                                display:block;
                                color:$color;
                                font-size:1.3rem;
                                text-align:center;
                                transition:all .5s;
                            }
                            .iconfont{
                                display:block;
                                color:darken($color,30);
                                font-size:1.5rem;
                            }
                            .icon-left{
                                text-align:left;
                            }
                            .icon-right{
                                text-align:right;
                            }
                            .sline{
                                margin-top:10px;
                                span{
                                    color:lighten($primaryColor,10);
                                    font-size:1.5rem;
                                    padding-left:10px;
                                    padding-right:10px;
                                }
                            }
                            .tline{
                                margin-top:10px;
                            }
                            @media screen and (max-width: 35.5em){
                                .fline,
                                .sline,
                                .tline,{
                                    font-size:1rem;
                                }
                                .sline{
                                    span{
                                        font-size:1.2rem;
                                    }
                                }
                                .iconfont{
                                    font-size:1.2rem;
                                }
                            }
                            @media screen and (max-width: 26em){
                                .fline,
                                .sline,
                                .tline,{
                                    font-size:.7rem;
                                }
                                .sline{
                                    span{
                                        font-size:.9rem;
                                    }
                                }
                                .iconfont{
                                    font-size:.9rem;
                                }
                            }
                        }
                    }
                }
                .data-two{
                    &:before{
                        right:-60px;
                    }
                    &:after{
                        right:-130px;
                    }
                    .t-ba{
                        right:-400px;
                    }
                    .pic{
                        background:url('../assets/imgs/eagle.png') no-repeat left center / auto 100%;
                    }
                }
                .data-five{
                    &:before{
                        left:-60px;
                    }
                    &:after{
                        left:-130px;
                    }
                    .t-ba{
                        left:-400px;
                    }
                    .pic{
                        background:url('../assets/imgs/hope.png') no-repeat right center / auto 100%;
                    }
                }
                .data-three,
                .data-four,
                .data-six{
                    .title{
                        .word{
                            font-size:1.2rem;
                            font-family:'Source-Han-Ligha050dfce518b79','Microsoft YaHei';
                            text-align:center;
                            position:relative;
                            &:before,
                            &:after{
                                content:'';
                                position:absolute;
                                top:49%;
                                height:1px;
                                width:30%;
                                background:#cacaca;
                            }
                            &:before{
                                left:10%;
                            }
                            &:after{
                                right:10%;
                            }
                        }
                    }
                }
                .data-three{
                    margin-top:40px;
                    padding-right:30px;
                    padding-left:30px;
                    .gallery{
                        margin-top:30px;
                        .imgEff{
                            border:3px solid #e6e6e6;
                            box-sizing:border-box;
                            border-radius:10px;
                            cursor:pointer;
                        }
                    }
                }
                .data-four{
                    margin-top:30px;
                    margin-left:10px;
                    margin-right:10px;
                    .proIntro{
                        margin-top:20px;
                        @for $val from 1 through 7{
                            .intro{
                                &:nth-child(7n+#{$val}){
                                    .container{
                                        .name{
                                            // background:lighten(nth($proColorList,$val),13);
                                            color:darken(nth($proColorList,$val),30);
                                            @include clg(
                                                lighten(nth($proColorList,$val),13),
                                                nth($proColorList,$val),
                                                nth($secColorList,$val),
                                                -45deg
                                            );
                                            .tag{
                                                background:darken(nth($proColorList,$val),3);
                                                color:lighten(nth($proColorList,$val),30)
                                            }
                                            .tecs{
                                                .tec{
                                                    color:darken(nth($proColorList,$val),20);
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .intro{
                        .container{
                            margin:10px;
                            box-shadow:0 0 20px rgba(0,0,0,0.1);
                            .name{
                                padding:30px;
                                font-size:1.5rem;
                                position:relative;
                                font-family:'LiDeBiao-Xing3a9a47d75318b79';
                                box-shadow:0px 10px 10px -5px rgba(0,0,0,0.1);
                                z-index:2;
                                .tag{
                                    position:absolute;
                                    height:10px;
                                    line-height:12px;
                                    width:40px;
                                    right:0px;
                                    top:15px;
                                    padding-top:5px;
                                    padding-bottom:5px;
                                    text-align:center;
                                    font-size:0.8rem;
                                    overflow:hidden;
                                    border-top-left-radius:8px;
                                    border-bottom-left-radius:8px;
                                }
                                .tecs{
                                    margin-top:5px;
                                    margin-bottom:10px;
                                    font-size:12px;
                                    width:100%;
                                    height:25px;
                                    .tec{
                                        padding:3px;
                                        margin:2px;
                                        display:inline-block;
                                        border-bottom:1px solid rgba(0,0,0,0.1);
                                    }
                                }
                            }
                            .main{
                                margin:0;
                                padding:15px;
                                height:150px;
                                font-weight:100;
                                font-size:1rem;
                                line-height:24px;
                                text-align:left;
                                background:#fff;
                                position:relative;
                                color: rgb(135, 135, 135);
                            }   
                        }
                    }
                }
                .data-six{
                    margin-top:30px;
                }
            }
            .foot{
                color:#b9b9b9;
                padding-top:30px;
                text-align:center;
                @media screen and (max-width: 35.5em){
                    .infoP{
                        margin-left:40px;
                        margin-right:40px;
                    }
                }
                .infoP{
                    background:rgba(0,0,0,0.8);
                    padding-top:25px;
                    padding-bottom:25px;
                    position:relative;
                    border:1px solid darken($primaryColor,40);
                    &:before{
                        content:'';
                        display:inline-block;
                        position:absolute;
                        top:-10px;
                        left:-10px;
                        right:-10px;
                        bottom:-10px;
                        border:4px solid darken($primaryColor,30);
                        z-index:-1;
                    }
                    .design{
                        .name{
                            font-family:'Source-Han-Ligha050dfce518b79','Microsoft YaHei';
                        }
                    }
                    .mail,
                    .phone{
                        margin-top:10px;
                    }
                    .mail{
                        a{
                            text-decoration:none;
                            color:#b9b9b9;
                        }
                    }
                }
                .thanksP{
                    margin-top:25px;
                    .thanks{
                        background:rgba(0,0,0,0.8);
                        padding-top:10px;
                        padding-bottom:10px;
                        font-family:'LiDeBiao-Xing3a9a47d75318b79';
                        font-size:1.2rem;
                    }
                    .link{
                        height:50px;
                        line-height:50px;
                        background:darken($primaryColor,5);
                        a{
                            display:inline-block;
                            text-decoration:none;
                            color:lighten($primaryColor,30);
                            height:100%;
                            width:100%;
                            position:relative;
                            transition:all 0.5s;
                            font-family:'Source-Han-Ligha050dfce518b79','Microsoft YaHei';
                            &:hover{
                                background:darken($primaryColor,13);
                            }
                            &:before{
                                position:absolute;
                                content:'';
                                display:inline-block;
                                width:2px;
                                height:70%;
                                top:15%;
                                background:darken($primaryColor,13);
                                right:-1px;
                            }
                        }
                    }
                }
            }
        }
    }
</style>


